*,
::after,
::before {
    box-sizing: border-box;
}

@import url(../src/colors.css);

body {
    font-family: 'Roboto';
    margin: 0;
    padding: 0;
    color: var(--black);
}

@font-face {
    font-family: "Roboto";
    font-weight: 400;
    src: url(../public/font/Roboto-Medium.ttf);
}

@font-face {
    font-family: "Roboto";
    font-weight: 300;
    src: url(../public/font/Roboto-Light.ttf);
}